    },
    {
      category: "targetStory",
      categoryTitle: "ターゲットストーリの有無",
      select: [],
      selectText: "All",
      filterText: []
    },
    {
      category: "usetech",
      categoryTitle: "使用想定技術",
      select: [],
      selectText: "All",
      filterText: []
    }
  ];

  private confirmBtn() {
    this.$emit("reload", this.newFilterText);
    this.$emit("close");
  }

  beforeMount() {
    this.newFilterText = this.filterTexts;
  }
}
